<template>
  <div class="pagination">

    <!-- {{startNumAndEndNum}} -->
    <button :disabled="pageNo==1" @click="$emit('getPageNo',pageNo-1)">
      上一页
    </button>
    <button :class="{active:pageNo==1}" v-if="startNumAndEndNum.start>=2" @click="$emit('getPageNo',1)">
      1
    </button>
    <button class="dotted" v-if="startNumAndEndNum.start>=3">...</button>

    <button v-for='page in startNumAndEndNum.end' :key='page' v-if="page>=startNumAndEndNum.start" :class="{active:pageNo==page}" @click="$emit('getPageNo',page)">
      {{page}}
    </button>

    <button class="dotted" v-if="startNumAndEndNum.end<=totalPage-2">...</button>
    <button v-if="startNumAndEndNum.end<=totalPage-1" :class="{active:pageNo==totalPage}" @click="$emit('getPageNo',totalPage)">
      {{totalPage}}
    </button>
    <button class="next" :disabled="pageNo==totalPage" @click="$emit('getPageNo',pageNo+1)">
      下一页
    </button>
    <button style="margin-left: 30px">共 {{ total }} 条</button>
  </div>
  </div>
</template>

<script>
export default {
  name:"Pagination",
  props:['total','pageNo','pageSize','continues'],
  computed:{
    totalPage(){
      return Math.ceil(this.total/this.pageSize);
    },
    startNumAndEndNum(){
      const {pageNo,continues,totalPage} = this;
      let start = 0;
      let end = 0;

      if(totalPage<continues){
        start = 1;
        end = totalPage;
      }else{
        start = pageNo -  parseInt(continues / 2);
        end = pageNo + parseInt(continues / 2);

        if(start <1){
          start = 1;
          end = continues;
        }
        if(end >totalPage){
          start = totalPage- continues +1;
          end = totalPage
        }
      }

      return {start,end}
    }
  }

}
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;
  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #409eff;
      color: #fff;
    }
  }
}
</style>